.services{
  display: block;
  position: relative;
  width: 100%;
  float: left;
  background-color: #fff;
  /*background-image: url("../img/services-bg.png");
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  background-size: cover;*/
  overflow: hidden;
  .square{
    display: block;
    position: absolute;
    top: 0;
    left: 48.66%;
    width: 22px;
    height: 15px;
    background-color: red;
  }
  .square-bottom{
    display: block;
    position: absolute;
    bottom: 0;
    left: 356px;
    width: 22px;
    height: 15px;
    background-color: #3c8af1;
  }
  &__animation1{
    display: block;
    position: absolute;
    width: 49%;
    left: 0;
    height: 56px;
    &--vertical{
      display: block;
      position: absolute;
      width: 1px;
      height: 0%;
      right: 0;
      background-color: red;
      transition: height 3s;
    }
  }
  &__animation2{
    position: absolute;
    left: 0;
    top: 55px;
    display: block;
    width: 49%;
    height: 111px;
    &--horizontal{
      display: block;
      position: absolute;
      width: 0;
      height: 1px;
      left: 100%;
      top: 0;
      background-color: red;
      transition: left 3s, width 3s;
    }
    &--horizontal2{
      display: block;
      position: absolute;
      width: 0;
      height: 1px;
      left: 0;
      bottom: 0;
      background-color: red;
      transition:  width 3s;
    }
    &--vertical{
      display: block;
      position: relative;
      width: 1px;
      height: 0;
      background-color: red;
      left: 0;
      top: 0;
      transition: height 3s;
    }
  }
  &__animation3{
    position: absolute;
    right: -240px;
    top: 55px;
    display: block;
    width: 58%;
    height: 111px;
    &--horizontal{
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0;
      height: 1px;
      background-color: red;
      transition: width 3s;
    }
  }
  &__animation4{
    display: block;
    position: absolute;
    width: 7%;
    height: 87%;
    bottom: 0;
    left: -248px;
    &--horizontal{
      display: block;
      position: absolute;
      width: 0;
      height: 1px;
      top: 0;
      left: 0;
      background-color: #3c8af1;
      transition: width 3s;
    }
    &--vertical{
      display: block;
      position: absolute;
      width: 1px;
      height: 0;
      top: 0;
      right: 0;
      background-color: #3c8af1;
      transition: height 3s;
    }
  }
  &__animation5{
    position: absolute;
    bottom: 0;
    left: -168px;
    display: block;
    width: 24.66%;
    height: 8%;
    border-left: 1px solid white;
    &--horizontal{
      display: block;
      position: absolute;
      width: 0;
      height: 1px;
      top: 0;
      left: 0;
      background-color: #3c8af1;
      transition: width 3s;
    }
    &--vertical{
      display: block;
      position: absolute;
      width: 1px;
      height: 0;
      top: 0;
      right: 0;
      background-color: #3c8af1;
      transition: height 3s;
    }
  }
  &__title{
    display: block;
    position: relative;
    width: 30%;
    margin: 0px auto;
    margin-top: 100px;
    margin-bottom: 60px;
    text-align: center;
    color: #ca2536;
    font-family: $gilroylight;
    font-size: 76px;
    text-transform: uppercase;
    padding: 20px;
    border: 1px solid #ca2536;
    z-index: 2;
    background-color: white;
    &--before{
      display: block;
      position: absolute;
      width: 56.66%;
      height: 200px;
      left: 159px;
      top: -25px;
      background-image: url("../img/services-bg-before.png");
      background-repeat: no-repeat;
      -webkit-background-size: contain;
      background-size: contain;
    }
    &--after{
      display: block;
      position: absolute;
      right: -247px;
      top: 123px;
      width: 56.66%;
      height: 85px;
      background-image: url("../img/services-bg-after.png");
      background-repeat: no-repeat;
      -webkit-background-size: contain;
      background-size: contain;
    }
  }
  &__box{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    min-height: 341px;
    position: relative;
    float: left;
    &--item{
      flex: 1 0 0;
      position: relative;
      float: left;
      text-align: center;
      opacity: 0;
      p{
        position: relative;
        width: 100%;
        color: #3c8bf2;
        font-family: $gilroyextrabold;
        font-size: 20px;
        line-height: 28px;
      }
    }
    &--img{
      display: block;
      position: relative;
      width: 100%;
      float: left;
    }
  }
  .brand__box--order{
    display: inline-block;
    position: relative;
    float: none;
    margin: 0px auto;
    margin-top: 60px;
    margin-bottom: 100px;
    padding: 15px 30px;
    border: 4px solid #3c8af1;
    text-transform: uppercase;
    color: #3c8bf2;
    font-family: $gilroyextrabold;
    font-size: 20px;
    &:hover{
      border: 4px solid #ca2536;
      color: #ca2536;
    }
  }
}